<template>
  <div class="reports">
    <keep-alive include="history, dashboard">
      <router-view></router-view>
    </keep-alive>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'Reports',
  computed: {
    ...mapGetters(['display'])
  },
  watch: {
    display() {
      if (
        this.display.name.toLowerCase() === 'dashboard' ||
        this.display.name.toLowerCase() === 'history'
      ) {
        this.$router.push({ name: this.display.name.toLowerCase() })
      }
    }
  }
}
</script>
<style lang="scss">
@import '~styles';

.reports {
  display: flex;
  height: 100%;
  width: 100%;
  padding: 20px;
  @media (max-width: $tablet-width) {
    padding: 10px;
  }

  @media (min-width: $desktop-min-width) {
    flex-direction: column;
    align-items: center;
  }

  &_section {
    max-width: 1240px;
    @media (min-width: $desktop-min-width) {
      flex: 1;
      padding: 20px;
    }
  }
}
</style>
